import { ChangeDetectionStrategy, Component, Input, OnChanges, OnInit, SimpleChanges, ViewChild } from '@angular/core';
import { CommonModule } from '@angular/common';
import { NgbCarousel, NgbCarouselModule } from '@ng-bootstrap/ng-bootstrap';
import { PlaceComponent } from '../place/place.component';
import { NewTripComponent } from '../new-trip/new-trip.component';
import { Trip } from 'src/app/models/trip.model';
import { BreadcrumpComponent } from '../common/breadcrump/breadcrump.component';

@Component({
  selector: 'app-planning',
  standalone: true,
  imports: [CommonModule, NgbCarouselModule, PlaceComponent, NewTripComponent, BreadcrumpComponent],
  templateUrl: './planning.component.html',
  styleUrls: ['./planning.component.scss'],
  changeDetection: ChangeDetectionStrategy.OnPush
})
export class PlanningComponent implements OnChanges, OnInit {

  @ViewChild('ngcarousel', { static: true }) ngCarousel!: NgbCarousel;
  @Input() trip!: Trip;
  places: string[] = [];
  childFormDirty: boolean;

  ngOnInit(): void {
    if (this.trip) {
      this.places = this.trip.places.map(place => place.name);
    }
  }

  ngOnChanges(changes: SimpleChanges) {
    let tripChanges = changes['trip'];
    if (tripChanges && tripChanges.previousValue && tripChanges.currentValue && tripChanges.previousValue.places !== tripChanges.currentValue.places) {
      this.places = tripChanges.currentValue?.places.map(place => place.name);
    }
  }

  // Move to specific slide
  navigateToSlide(item: any) {
    this.ngCarousel.select('slide-' + item);
  }

  // Move to next/previous slide
  navigate(isNext: boolean) {
    if(isNext) {
      this.ngCarousel.next();
    } else {
      this.ngCarousel.prev();
    }
  }
}