import { createAction, props } from "@ngrx/store";
import { Place, Trip } from "../models/trip.model";
import { CheckList } from "../models/checklist.model";

export const getTrips = createAction(
    '[Trip/API] Get Trips'
);

export const getPastTrips = createAction(
    '[Trip/API] Get Past Trips'
);

export const getTripsSuccessResponse = createAction(
    '[Trip] Get Trips Success', 
    props<{ trips: Trip[], tripsChanged: boolean }>()
);

export const newTrip = createAction(
    '[Trip/API] New Trip',
    props<{ trip: Trip }>()
);

export const newTripSuccessResponse = createAction(
    '[Trip] New Trip Success', 
    props<{ trip: Trip, id: string }>()
);

export const updateTrip = createAction(
    '[Trip/API] Update Trip',
    props<{ trip: Trip }>()
);

export const updatePlace = createAction(
    '[Trip/API] Update Place',
    props<{ place: Place }>()
);

export const updateBag = createAction(
    '[Trip/API] Update Bag',
    props<{ bag: CheckList[] }>()
);

export const updatePrepareList = createAction(
    '[Trip/API] Update PrepareCheck',
    props<{ prepareCheck: CheckList[] }>()
);

// export const updateTripDb = createAction(
//     '[Trip/API] Update Trip at DB'
// );

export const updateTripSuccessResponse = createAction(
    '[Trip] Update Trip Success'
);

export const getFailureResponse = createAction(
    '[Trip] Get Failure',
    props<{ error: string }>()
);

export const selectTrip = createAction(
    '[Trip] Selected Trip',
    props<{tripId: string}>()
);

export const clearTripSelection = createAction(
    '[Trip] Clear Trip Selection'
);
