import { Component, Input, OnInit } from '@angular/core';
import { CommonModule } from '@angular/common';
import { FormsModule } from '@angular/forms';
import { CheckList } from 'src/app/models/checklist.model';
import { Trip } from 'src/app/models/trip.model';
import { Store } from '@ngrx/store';
import { TripStoreState } from 'src/app/models/tripStoreState';
import { componentBase } from '../componentBase';
import * as tripActions from 'src/app/store/trip.action';

@Component({
  selector: 'app-offcanvasbag',
  standalone: true,
  imports: [CommonModule, FormsModule],
  templateUrl: './offcanvasbag.component.html',
  styleUrls: ['./offcanvasbag.component.scss']
})
export class OffcanvasbagComponent extends componentBase implements OnInit {
  isOpen: boolean = false;
  @Input() trip: Trip;
  userInput: string = '';
  bagItems: CheckList[] = [];
  isChanged: boolean = false;

  constructor(store: Store<TripStoreState>) {
    super(store);
  }

  ngOnInit(): void {
    if (this.trip) {
      this.bagItems = this.trip.bag ?? [];
    }
  }

  get sortedItems() {
    return [...this.bagItems].sort((a, b) => {
      if (a.checked && !b.checked) {
        return 1;
      } else if (!a.checked && b.checked) {
        return -1;
      } else {
        return 0;
      }
    });
  }

  addtoCheckList() {
    if (this.userInput.length > 0 && !this.bagItems.find(item => item.name === this.userInput)) {
        this.bagItems = [...this.bagItems, { name: this.userInput, checked: false }];
    }
    this.userInput = "";
    this.markasChanged();
  }

  markasChanged() {
    if (!this.isChanged) {
      this.isChanged = true;
    }
  }

  removefromCheckList(value: string) {
    this.bagItems = this.bagItems.filter(s => s.name != value);
    this.markasChanged();
  }

  onCheckedChange(item, event) {
    const updatedItem = { ...item, checked: event.target.checked };
    const index = this.bagItems.indexOf(item);
    this.bagItems = [...this.bagItems.slice(0, index), updatedItem, ...this.bagItems.slice(index + 1)];
    this.markasChanged();
  }

  toggleOffcanvas() {
    this.isOpen = !this.isOpen;
  }

  showOffCanvas() {
    this.isOpen = true;
  }

  closeOffCanvas() {
    this.isOpen = false;
  }

  onHidden() {
    // this.reactiveForm.reset();
    // this.setFormState();
  }

  save() {
    this.store.dispatch(tripActions.updateBag({ bag : this.bagItems }));
    this.isChanged = false;
  }

  cancelChanges() {
    this.bagItems = this.trip.bag ?? [];
    this.isChanged = false;
  }
}
