import { ChangeDetectionStrategy, Component, Input, OnInit, ViewChild } from '@angular/core';
import { CommonModule } from '@angular/common';
import { Trip } from 'src/app/models/trip.model';
import { TripdetailComponent } from '../tripdetail/tripdetail.component';
import { Observable } from 'rxjs';
import { select, Store } from '@ngrx/store';
import { selectTrips} from 'src/app/store/trip.selectors';
import * as tripActions from 'src/app/store/trip.action';
import { componentBase } from '../componentBase';
import { NgbModal, NgbModalConfig } from '@ng-bootstrap/ng-bootstrap';
import { TripStoreState } from 'src/app/models/tripStoreState';

@Component({
  selector: 'app-triplist',
  standalone: true,
  imports: [CommonModule, TripdetailComponent],
  templateUrl: './triplist.component.html',
  styleUrls: ['./triplist.component.scss'],
  changeDetection: ChangeDetectionStrategy.OnPush
})
export class TriplistComponent extends componentBase implements OnInit {
  @ViewChild('modelControl') modelControl: NgbModal;
  trips$: Observable<Trip[]>;
  title: string;
  @Input() isPast: boolean = false;

  constructor(store: Store<TripStoreState>, 
    private ngbModelconfig: NgbModalConfig, 
    private modalService: NgbModal) {
      super(store);
      this.trips$ = store.pipe(select(selectTrips))
      ngbModelconfig.backdrop = 'static';
      ngbModelconfig.keyboard = false;
  }

  ngOnInit() {
    if (!this.isPast) {
      this.store.dispatch(tripActions.getTrips());
    } else {
      //this.store.dispatch(tripActions.getPastTrips());
      this.store.dispatch(tripActions.getTrips());
    }
  }

  getPlacesHeading(trip: Trip) {
    return trip.places.map(s => s.name);
  }

  showTripDetails($event?: any) {
    this.title = ($event?.tripId) ? "Edit Trip" : "New Trip";
    if ($event?.tripId) {
      this.store.dispatch(tripActions.selectTrip({tripId : $event.tripId}));
    } 
    this.modalService.open(this.modelControl, { scrollable: true, size: 'xl' }).result.then(
      (result) => {
        //this.updateDb();
        this.clearSelection();
      },
      (reason) => {
        //this.updateDb();
        this.clearSelection();
      }
    );
  }

  // updateDb() {
  //   this.store.pipe(select(selectTripsChanged), first()).subscribe(value => {
  //     if (!!value) {
  //       this.store.dispatch(tripActions.updateTripDb());
  //     }
  //   });
  //   this.store.dispatch(tripActions.clearTripSelection());
  // }

  clearSelection() {
    this.store.dispatch(tripActions.clearTripSelection());
  }
}
