import { Component, ElementRef, EventEmitter, Input, OnChanges, Output, ViewChild, forwardRef } from '@angular/core';
import { CommonModule } from '@angular/common';
import { AbstractControl, ControlValueAccessor, FormsModule, NG_VALIDATORS, NG_VALUE_ACCESSOR, ValidationErrors, Validator } from '@angular/forms';
import { DropdownOption } from 'src/app/models/dropdownOption';
import { NgbCollapseModule } from '@ng-bootstrap/ng-bootstrap';

@Component({
  selector: 'app-selectformtag',
  standalone: true,
  imports: [CommonModule, FormsModule, NgbCollapseModule],
  templateUrl: './selectformtag.component.html',
  styleUrls: ['./selectformtag.component.scss'],
  providers: [
    {
      provide: NG_VALUE_ACCESSOR,
      multi:true,
      useExisting: forwardRef(() => SelectformtagComponent) 
    },
    {
      provide: NG_VALIDATORS,
      multi: true,
      useExisting: forwardRef(() => SelectformtagComponent) 
    }
  ]
})
export class SelectformtagComponent implements OnChanges, ControlValueAccessor, Validator {
  @ViewChild('checkInput') checkInput: ElementRef<HTMLInputElement>;
  @Input() toggleLabel!: string
  @Input() items!: DropdownOption[]
  @Input() showDetails: boolean = true
  @Input() showSelectAll: boolean = false
  @Input() allowEmptyTags: boolean = false
  isCollapsed: boolean = true;
  tags!: string[];
  value: string = "-1";
  isSelectAll: boolean = false;
  @Output() onTagClick = new EventEmitter<string>();

  constructor() {
  }

  ngOnChanges() {
    if (this.items && this.checkInput?.nativeElement?.checked) {
      this.checkInput.nativeElement.checked = false;
    }
  }
  
  onOptionsSelected(event: any) {
    this.value = event.target.value;
    this.addtoTags();
    event.target.value = "-1";
    this.isCollapsed = true;
  }
  
  onSelectAllChange(event: any) {
    if (event.target.checked) {
      this.tags = [];
      this.tags = this.items.map(s => s.text);
    } else {
      this.tags = [];
    }
    this.onChange(this.tags);
    this.isCollapsed = true;
  }

  addtoTags() {
    this.markAsTouched();
    if (this.value !== "-1" && !this.tags.includes(this.value)) {
        this.tags = [...this.tags, this.value];
        this.onChange(this.tags);
    } 
  }

  removeTag(value: string) {
    this.markAsTouched();
    this.tags = this.tags?.filter(s => s != value);
    this.resetSelectAll();
    this.onChange(this.tags);
  }

  openDetails(tag: string) {
    // const offCancaseRef = this.offCanvasService.open(OffcanvasComponent, { position: 'end' });
    // offCancaseRef.componentInstance.tagName = tag;
    this.onTagClick.emit(tag);
  }

  touched = false;
  disabled = false;

  writeValue(inputTags: string[]): void {
    this.tags = inputTags ?? [];
    this.resetSelectAll();
  }

  resetSelectAll() {
    if (this.tags.length === 0 && this.checkInput?.nativeElement?.checked) {
      this.checkInput.nativeElement.checked = false;
    }
  }

  registerOnChange(onChangeFn: any): void {
    this.onChange = onChangeFn;
  }

  markAsTouched() {
    if (!this.touched) {
      this.onTouched();
      this.touched = true;
    }
  }

  validate(control: AbstractControl<any, any>): ValidationErrors | null {
    return (!this.allowEmptyTags && this.tags?.length === 0) ? { tagsAreEmpty: true } : null;
  }

  registerOnValidatorChange?(fn: () => void): void {
    //throw new Error('Method not implemented.');
  }

  onChange = (tags) => {};

  registerOnTouched(onTouchedFn: any): void {
    this.onTouched = onTouchedFn;
  }

  onTouched = () => {};

  setDisabledState?(isDisabled: boolean): void {
    this.disabled = isDisabled;
  }
}