import { ChangeDetectionStrategy, Component, EventEmitter, Input, OnChanges, OnDestroy, Output } from '@angular/core';
import { CommonModule } from '@angular/common';
import { SelectformtagComponent } from '../selectformtag/selectformtag.component';
import { FormBuilder, FormControl, FormGroup, ReactiveFormsModule, Validators } from '@angular/forms';
import { offcanvas } from 'src/app/models/offcanvas.model';
import { Tag } from 'src/app/models/trip.model';
import { Subscription } from 'rxjs';

@Component({
  selector: 'app-offcanvasdetails',
  standalone: true,
  imports: [CommonModule, ReactiveFormsModule, SelectformtagComponent],
  templateUrl: './offcanvasdetails.component.html',
  styleUrls: ['./offcanvasdetails.component.scss'],
  changeDetection: ChangeDetectionStrategy.OnPush
})
export class OffcanvasdetailsComponent implements OnChanges, OnDestroy {
  isOpen: boolean = false;
  @Input() data: offcanvas = {};
  reactiveForm: FormGroup;
  formValid: boolean = false;
  formDirty: boolean = false;
  @Output() onSave = new EventEmitter<offcanvas>();
  title: string = '';
  initialValue!: Tag;
  commentsSubscription: Subscription;
  costSubscription: Subscription;
  paidBySubscription: Subscription;
  sharedWithSubscription: Subscription;
  

  constructor(private fb: FormBuilder) {
    const numberRegExp = /^\d+(\.\d+)?$/;
    
    this.reactiveForm = this.fb.group({
      comments: new FormControl<string | null>(null),
      cost: new FormControl<string | null>(null, [Validators.pattern(numberRegExp)]),
      paidBy: new FormControl<string[] | null>(null),
      sharedWith: new FormControl<string[] | null>([])
    });

    this.initialValue = { name: this.data.tag?.name ?? null };

    this.commentsSubscription = this.reactiveForm.get('comments').valueChanges.subscribe(changes => {
      if (changes) {
        this.data = {...this.data, tag: {...this.data.tag, comments: changes}};
        this.setFormState();
      }
    });

    this.costSubscription = this.reactiveForm.get('cost').valueChanges.subscribe(changes => {
      if (changes) {
        this.data = {...this.data, tag: {...this.data.tag, expense: {...this.data.tag.expense, cost: changes }}};
        this.setFormState();
      }
    });

    this.paidBySubscription = this.reactiveForm.get('paidBy').valueChanges.subscribe(changes => {
      if (changes) {
        const newitems = changes?.filter(value => !this.data.tag.expense?.paidBy?.find(obj => obj === value)) ?? [];
        const removeItems = this.data.tag.expense?.paidBy?.filter(name => changes.includes(name)) ?? [];  
        this.data = {...this.data, tag: {...this.data.tag, expense: {...this.data.tag.expense, paidBy: removeItems.concat(newitems)}}};
        this.setFormState();
      }
    });

    this.sharedWithSubscription = this.reactiveForm.get('sharedWith').valueChanges.subscribe(changes => {
      if (changes) {
        const newitems = changes?.filter(value => !this.data.tag.expense?.sharedWith?.find(obj => obj === value)) ?? [];
        const removeItems = this.data.tag.expense?.sharedWith?.filter(name => changes.includes(name)) ?? [];  
        this.data = {...this.data, tag: {...this.data.tag, expense: {...this.data.tag.expense, sharedWith: removeItems.concat(newitems)}}};
        this.setFormState();
      }
    });
  }

  ngOnChanges() {
    if (this.data) {
      this.initialValue = {...this.initialValue, 
        name: this.data.tag?.name ?? null,
        comments: this.data.tag?.comments ?? null,
        expense: this.data.tag?.expense ?? null
      }

      this.reactiveForm.patchValue({
        comments: this.data.tag?.comments ?? null,
        cost: this.data.tag?.expense?.cost ?? null,
        paidBy: this.data.tag?.expense?.paidBy ?? null,
        sharedWith: this.data?.tag?.expense?.sharedWith ?? null
      });
      this.title = `${this.data?.tag?.name}`;
    }
  }

  setFormState() {
    this.formValid = this.reactiveForm.valid;
    this.formDirty = this.reactiveForm.dirty;
  }

  toggleOffcanvas() {
    this.isOpen = !this.isOpen;
  }

  showOffCanvas() {
    this.isOpen = true;
  }

  closeOffCanvas() {
    this.isOpen = false;
  }

  onHidden() {
    this.reactiveForm.reset();
    this.setFormState();
  }

  save() {
    if (this.formValid && this.formDirty) {
      this.onSave.emit(this.data);
      this.reactiveForm.markAsPristine();
      this.setFormState();
    }
  }

  cancel() {
    this.reactiveForm.reset({
      comments: this.initialValue?.comments,
      cost: this.initialValue?.expense?.cost,
      paidBy: this.initialValue?.expense?.paidBy,
      sharedWith: this.initialValue?.expense?.sharedWith,
    });
    this.setFormState();
  }

  ngOnDestroy() {
    if (this.commentsSubscription) {
      this.commentsSubscription.unsubscribe();
    }
    if (this.costSubscription) {
      this.costSubscription.unsubscribe();
    }
    if (this.paidBySubscription) {
      this.paidBySubscription.unsubscribe();
    }
    if (this.sharedWithSubscription) {
      this.sharedWithSubscription.unsubscribe();
    }
  }
}