import { Injectable } from '@angular/core';
import { AngularFirestore, AngularFirestoreCollection } from '@angular/fire/compat/firestore';
import { Observable } from 'rxjs';
import { Trip } from '../models/trip.model';
import { AuthService } from './auth.service';

@Injectable({
  providedIn: 'root'
})
export class TripService {
  firestoreCollection: AngularFirestoreCollection;

  constructor(private firestore: AngularFirestore, private authService: AuthService) { 
    this.firestoreCollection = firestore.collection<Trip>('trips');
  }

  getTrips() : Observable<Trip[]> {
    return this.firestore.collection<Trip>('trips',  
      ref => ref.where('userId', '==', this.authService.user.uid)
      .orderBy('start_at'))
      .valueChanges({ idField: 'id'});
  }

  getPastTrips() : Observable<Trip[]> {
    const today = new Date();
    const dateString = today.getFullYear() + '-' + (today.getMonth() + 1) + '-' + today.getDate();
    return this.firestore.collection<Trip>('trips',  
      ref => ref.where('userId', '==', this.authService.user.uid).where('end_at', '<', dateString)
      .orderBy('end_at', 'desc'))
      .valueChanges({ idField: 'id'});
  }

  addNewTrip(trip: Trip) {
    return this.firestoreCollection.add(trip);
  }

  updateTrip(trip: Trip) {
    return this.firestoreCollection.doc(trip.id).update({...trip});
  }

  updatePlace(trip: Trip) {
    return this.firestoreCollection.doc(trip.id).set({places: trip.places}, { merge: true });
  }

  updateBag(trip: Trip) {
    return this.firestoreCollection.doc(trip.id).set({bag: trip.bag}, { merge: true });
  }

  updatePrepareCheck(trip: Trip) {
    return this.firestoreCollection.doc(trip.id).set({prepareCheck: trip.prepareCheck}, { merge: true });
  }
}
