import { Component, EventEmitter, Input, Output, forwardRef } from '@angular/core';
import { CommonModule } from '@angular/common';
import { AbstractControl, ControlValueAccessor, FormsModule, NG_VALIDATORS, NG_VALUE_ACCESSOR, ValidationErrors, Validator } from '@angular/forms';
import { NgbCollapseModule } from '@ng-bootstrap/ng-bootstrap';
import { startCase } from 'lodash';

@Component({
  selector: 'app-textformtag',
  standalone: true,
  imports: [CommonModule, FormsModule, NgbCollapseModule],
  templateUrl: './textformtag.component.html',
  styleUrls: ['./textformtag.component.scss'],
  providers: [
    {
      provide: NG_VALUE_ACCESSOR,
      multi:true,
      useExisting: forwardRef(() => TextformtagComponent) 
    },
    {
      provide: NG_VALIDATORS,
      multi: true,
      useExisting: forwardRef(() => TextformtagComponent) 
    }
  ]
})
export class TextformtagComponent implements ControlValueAccessor, Validator {
  @Input() toggleLabel!: string
  @Input() placeholderText!: string
  @Input() allowEmptyTags: boolean = false
  @Output() onTagClick = new EventEmitter<string>();

  isCollapsed: boolean = true;
  userInput: string = '';
  tags: string[] = [];

  constructor() { }

  ngOnInit(): void {
  }

  addtoTags() {
    this.markAsTouched();
    if (this.userInput.length > 0 && this.tags?.indexOf(this.userInput) < 0) {
      let formatedInput = this.userInput.replace(/\w+/g, startCase);
      this.tags = [...this.tags, formatedInput];
      this.onChange(this.tags);
      this.userInput = "";
    }
    this.isCollapsed = true;
  }

  removeTag(value: string) {
    this.markAsTouched();
    this.tags = this.tags?.filter(s => s != value);
    this.onChange(this.tags);
  }

  openDetails(tag: string) {
    this.onTagClick.emit(tag);
  }
  
  touched = false;
  disabled = false;

  writeValue(inputTags: string[]): void {
    this.tags = inputTags;
  }

  registerOnChange(onChangeFn: any): void {
    this.onChange = onChangeFn;
  }

  markAsTouched() {
    if (!this.touched) {
      this.onTouched();
      this.touched = true;
    }
  }

  validate(control: AbstractControl<any, any>): ValidationErrors | null {
    return (!this.allowEmptyTags && this.tags?.length === 0) ? { tagsAreEmpty: true } : null;
  }

  registerOnValidatorChange?(fn: () => void): void {
    //throw new Error('Method not implemented.');
  }

  onChange = (tags) => {};

  registerOnTouched(onTouchedFn: any): void {
    this.onTouched = onTouchedFn;
  }

  onTouched = () => {};

  setDisabledState?(isDisabled: boolean): void {
    this.disabled = isDisabled;
  }
}