import { Component, Input, OnInit } from '@angular/core';
import { CommonModule } from '@angular/common';
import { NgbAccordionModule } from '@ng-bootstrap/ng-bootstrap';
import { PlanningComponent } from '../planning/planning.component';
import { PrepareComponent } from '../prepare/prepare.component';
import { componentBase } from '../componentBase';
import { Store, select } from '@ngrx/store';
import { TripStoreState } from 'src/app/models/tripStoreState';
import { Trip } from 'src/app/models/trip.model';
import { selectCurrentSelectedTrip } from 'src/app/store/trip.selectors';
import { distinctUntilChanged, tap } from 'rxjs';

@Component({
  selector: 'app-tripdetail',
  standalone: true,
  imports: [CommonModule, NgbAccordionModule, PlanningComponent, PrepareComponent],
  templateUrl: './tripdetail.component.html',
  styleUrls: ['./tripdetail.component.scss']
})
export class TripdetailComponent extends componentBase implements OnInit {
  @Input() activePanel: any = 'plan-panel';
  trip!: Trip;

  constructor(store: Store<TripStoreState>) {
    super(store);
    this.store.pipe(
      select(selectCurrentSelectedTrip),
      distinctUntilChanged(), 
      tap(data => {
        if (data) {
          this.trip = data;
        }
      })).subscribe();
  }

  ngOnInit(): void {
  }
}
