import { Injectable } from "@angular/core";
import { Actions, createEffect, ofType } from "@ngrx/effects";
import { select, Store } from "@ngrx/store";
import { catchError, from, map, of, switchMap, withLatestFrom } from "rxjs";
import { TripStoreState } from "../models/tripStoreState";
import { TripService } from "../services/trip.service";
import * as tripActions from "./trip.action";
import { selectCurrentSelectedTrip, selectTrips, selectTripsChanged } from "./trip.selectors";

@Injectable()
export class TripsEffects {
    constructor(private actions$: Actions, private tripService: TripService, private store: Store<TripStoreState>) {}

    getTrips$ = createEffect(() => 
        this.actions$.pipe(
            ofType(tripActions.getTrips),
            withLatestFrom(
                this.store.pipe(select(selectTripsChanged)),
                this.store.pipe(select(selectTrips))
            ),
            switchMap(([ action, tripsChanged, trips ]) => {
                // If tripsChanged is True then only API call needed, else can return the current trips
                if (tripsChanged === true) {
                    return this.tripService.getTrips().pipe(
                        map(response => tripActions.getTripsSuccessResponse({ trips: response, tripsChanged: false })),
                        catchError(error => of(tripActions.getFailureResponse({ error: error.message })))
                    )
                } else {
                    return of(tripActions.getTripsSuccessResponse({ trips, tripsChanged }));
                }
            })
        )
    );

    getPastTrips$ = createEffect(() => 
        this.actions$.pipe(
            ofType(tripActions.getPastTrips),
            withLatestFrom(
                this.store.pipe(select(selectTripsChanged)),
                this.store.pipe(select(selectTrips))
            ),
            switchMap(([ action, tripsChanged, trips ]) => {
                // If tripsChanged is True then only API call needed, else can return the current trips
                if (tripsChanged === true) {
                    return this.tripService.getPastTrips().pipe(
                        map(response => tripActions.getTripsSuccessResponse({ trips: response, tripsChanged: false })),
                        catchError(error => of(tripActions.getFailureResponse({ error: error.message })))
                    )
                } else {
                    return of(tripActions.getTripsSuccessResponse({ trips, tripsChanged }));
                }
            })
        )
    );

    newTrip$ = createEffect(() =>
        this.actions$.pipe(
            ofType(tripActions.newTrip),
            switchMap((payload) => 
                from(this.tripService.addNewTrip(payload.trip)).pipe(
                    map((response) => tripActions.newTripSuccessResponse({ trip: payload.trip, id: response.id })), 
                    catchError(error => of(tripActions.getFailureResponse({ error: error.message })))
                )
            )
        )
    );

    updateTrip$ = createEffect(() =>
        this.actions$.pipe(
            ofType(tripActions.updateTrip),
            withLatestFrom(
                this.store.pipe(select(selectCurrentSelectedTrip))
            ),
            switchMap(([action, trip]) => 
                this.tripService.updateTrip(trip)
                .then((response) => tripActions.updateTripSuccessResponse())
                .catch(error => tripActions.getFailureResponse({ error: error.message }))
            )
        )
    );

    updatePlace$ = createEffect(() =>
        this.actions$.pipe(
            ofType(tripActions.updatePlace),
            withLatestFrom(
                this.store.pipe(select(selectCurrentSelectedTrip))
            ),
            switchMap(([action, trip]) => 
                this.tripService.updatePlace(trip)
                .then((response) => tripActions.updateTripSuccessResponse())
                .catch(error => tripActions.getFailureResponse({ error: error.message }))
            )
        )
    );

    updateBag$ = createEffect(() =>
        this.actions$.pipe(
            ofType(tripActions.updateBag),
            withLatestFrom(
                this.store.pipe(select(selectCurrentSelectedTrip))
            ),
            switchMap(([action, trip]) => 
                this.tripService.updateBag(trip)
                .then((response) => tripActions.updateTripSuccessResponse())
                .catch(error => tripActions.getFailureResponse({ error: error.message }))
            )
        )
    );

    updatePrepareCheck$ = createEffect(() =>
        this.actions$.pipe(
            ofType(tripActions.updatePrepareList),
            withLatestFrom(
                this.store.pipe(select(selectCurrentSelectedTrip))
            ),
            switchMap(([action, trip]) => 
                this.tripService.updatePrepareCheck(trip)
                .then((response) => tripActions.updateTripSuccessResponse())
                .catch(error => tripActions.getFailureResponse({ error: error.message }))
            )
        )
    );
}