import { ChangeDetectionStrategy, Component, ElementRef, EventEmitter, Input, OnChanges, OnDestroy, OnInit, Output, SimpleChanges, ViewChild } from '@angular/core';
import { CommonModule } from '@angular/common';
import { NgbCalendar, NgbDate, NgbDateParserFormatter, NgbDatepicker, NgbDatepickerModule, NgbDateStruct } from '@ng-bootstrap/ng-bootstrap';
import { DropdownOption } from 'src/app/models/dropdownOption';
import { TransportOptions } from 'src/app/models/transports.const';
import { Activity, Place, Stay, Transport, Trip, Tag } from 'src/app/models/trip.model';
import { FormBuilder, FormControl, FormGroup, ReactiveFormsModule, Validators } from '@angular/forms';
import { Store } from '@ngrx/store';
import { TripStoreState } from 'src/app/models/tripStoreState';
import { componentBase } from '../componentBase';
import * as tripActions from 'src/app/store/trip.action';
import { SelectformtagComponent } from '../common/selectformtag/selectformtag.component';
import { OffcanvasdetailsComponent } from '../common/offcanvasdetails/offcanvasdetails.component';
import { offcanvas } from 'src/app/models/offcanvas.model';
import { TextformtagComponent } from '../common/textformtag/textformtag.component';
import { Subscription } from 'rxjs';

@Component({
  selector: 'app-place',
  standalone: true,
  imports: [CommonModule, ReactiveFormsModule, NgbDatepickerModule, TextformtagComponent, SelectformtagComponent, OffcanvasdetailsComponent],
  templateUrl: './place.component.html',
  styleUrls: ['./place.component.scss'],
  changeDetection: ChangeDetectionStrategy.OnPush
})
export class PlaceComponent extends componentBase implements OnInit, OnChanges, OnDestroy {
  @Input() slideIndex!: number;
  reactiveForm: FormGroup;
  @ViewChild(NgbDatepicker, { static: false, read: ElementRef }) datePicker: ElementRef<NgbDatepicker>;
  @ViewChild(OffcanvasdetailsComponent)
  private offcanvasdetailsComponent: OffcanvasdetailsComponent;
  @Input() trip!: Trip;
  @Output() onNavigate = new EventEmitter<boolean>();
  currentPlace: Place;
  transportOptions: DropdownOption[];
  formValid: boolean = false;
  formDirty: boolean = false;
  nextSlideExists: boolean = false;
  offCanvasData: offcanvas = {};
  fromDateSubscription: Subscription;
  toDateSubscription: Subscription;
  transportsSubscription: Subscription;
  staysSubscription: Subscription;
  activitiesSubscription: Subscription;

  constructor(private fb: FormBuilder, calendar: NgbCalendar, store: Store<TripStoreState>, private parserFormatter: NgbDateParserFormatter) {
    super(store);
    this.transportOptions = TransportOptions;
  }

  ngOnInit(): void {
    if (this.trip) {
      this.setupReactiveForm();
    }
    
    this.fromDateSubscription = this.reactiveForm.get('fromDate').valueChanges.subscribe(changes => {
      this.currentPlace = {...this.currentPlace, start_at: this.parserFormatter.format(changes)};
      this.setFormState();
    });

    this.toDateSubscription = this.reactiveForm.get('toDate').valueChanges.subscribe(changes => {
      this.currentPlace = {...this.currentPlace, end_at: this.parserFormatter.format(changes)};
      this.setFormState();
    });

    this.transportsSubscription = this.reactiveForm.get('transports').valueChanges.subscribe(changes => {
      const newitems = changes.filter(value => !this.currentPlace.transports?.find(obj => obj.name === value)) ?? [];
      const newTransports = newitems.map(s => { 
        return { name : s } as Transport;
      });
      const removeItems = this.currentPlace.transports?.filter(obj => changes.includes(obj.name)) ?? [];  
      this.currentPlace = {...this.currentPlace, transports: removeItems.concat(newTransports)};
      this.setFormState();
    });

    this.staysSubscription = this.reactiveForm.get('stays').valueChanges.subscribe(changes => {
      const newitems = changes.filter(value => !this.currentPlace.stays?.find(obj => obj.name === value)) ?? [];
      const newStays = newitems.map(s => { 
        return { name : s } as Stay;
      });
      const removeItems = this.currentPlace.stays?.filter(obj => changes.includes(obj.name)) ?? [];  
      this.currentPlace = {...this.currentPlace, stays: removeItems.concat(newStays)};
      this.setFormState();
    });

    this.activitiesSubscription = this.reactiveForm.get('activities').valueChanges.subscribe(changes => {
      const newitems = changes.filter(value => !this.currentPlace.activities?.find(obj => obj.name === value)) ?? [];
      const newActivities = newitems.map(s => { 
        return { name : s } as Activity;
      });
      const removeItems = this.currentPlace.activities?.filter(obj => changes.includes(obj.name)) ?? [];  
      this.currentPlace = {...this.currentPlace, activities: removeItems.concat(newActivities)};
      this.setFormState();
    });
  }

  setupReactiveForm(isReset: Boolean = false) {
    this.currentPlace = this.trip.places[this.slideIndex];
    this.nextSlideExists = !!this.trip.places[this.slideIndex + 1];

    let tripStartAt = new Date(this.trip.start_at);
    let tripEndAt = new Date(this.trip.end_at);
    this.minDate = new NgbDate(tripStartAt.getFullYear(), tripStartAt.getMonth() + 1, tripStartAt.getDate());
    this.maxDate = new NgbDate(tripEndAt.getFullYear(), tripEndAt.getMonth() + 1, tripEndAt.getDate());
    
    const { start_at, end_at } = this.currentPlace;
    let startAt = start_at ? new Date(start_at) : tripStartAt;
    let endAt = end_at ? new Date(end_at) : tripEndAt;
    let transports = this.currentPlace.transports?.map(obj => obj.name) ?? [];
    let stays = this.currentPlace.stays?.map(obj => obj.name) ?? [];
    let activities = this.currentPlace.activities?.map(obj => obj.name) ?? [];

    if (isReset) {
      this.reactiveForm.reset({
        fromDate: new NgbDate(startAt.getFullYear(), startAt.getMonth() + 1, startAt.getDate()),
        toDate: new NgbDate(endAt.getFullYear(), endAt.getMonth() + 1, endAt.getDate()),
        transports,
        stays,
        activities
      });
    } else {
      this.reactiveForm = this.fb.group({
        fromDate: new FormControl<NgbDate | null>(new NgbDate(startAt.getFullYear(), startAt.getMonth() + 1, startAt.getDate())),
        toDate: new FormControl<NgbDate | null>(new NgbDate(endAt.getFullYear(), endAt.getMonth() + 1, endAt.getDate())),
        transports: new FormControl<string[]>(transports),
        stays: new FormControl<string[]>(stays),
        activities: new FormControl<string[]>(activities)
      });
    }
  }

  setFormState() {
    this.formValid = this.reactiveForm.valid;
    this.formDirty = this.reactiveForm.dirty;
  }

  ngOnChanges(changes: SimpleChanges) {
    let tripIdChanges = changes['trip'];
    if (tripIdChanges && tripIdChanges.previousValue && tripIdChanges.currentValue && tripIdChanges.previousValue !== tripIdChanges.currentValue) {
      if (tripIdChanges.previousValue.start_at !== tripIdChanges.currentValue.start_at) {
        const startAt = new Date(tripIdChanges.currentValue.start_at);
        this.minDate = new NgbDate(startAt.getFullYear(), startAt.getMonth() + 1, startAt.getDate());
        this.reactiveForm.patchValue({fromDate: this.minDate});
      }
      if (tripIdChanges.previousValue.end_at !== tripIdChanges.currentValue.end_at) {
        const endAt = new Date(tripIdChanges.currentValue.end_at);
        this.maxDate = new NgbDate(endAt.getFullYear(), endAt.getMonth() + 1, endAt.getDate());
        this.reactiveForm.patchValue({toDate: this.maxDate});
      }

      if (tripIdChanges.previousValue.places.length !== tripIdChanges.currentValue.places.length) {
        this.nextSlideExists = !!this.trip.places[this.slideIndex + 1]
      }
    }
  }

  ngAfterViewInit() {
    // //const datePickerRef = this.datePicker.nativeElement;
    // if (this.trip) {
    //   this.datePicker.nativeElement.navigateTo({year: this.reactiveForm.value.fromDate.year, month: this.reactiveForm.value.fromDate.month});
    // }
  }

  ngOnDestroy(): void {
    if (this.fromDateSubscription) {
      this.fromDateSubscription.unsubscribe();
    }
    if (this.toDateSubscription) {
      this.toDateSubscription.unsubscribe();
    }
    if (this.transportsSubscription) {
      this.transportsSubscription.unsubscribe();
    }
    if (this.staysSubscription) {
      this.staysSubscription.unsubscribe();
    }
    if (this.activitiesSubscription) {
      this.activitiesSubscription.unsubscribe();
    }
  }

  tagClicked(property: string, tagName: string) {
    let tagData: offcanvas = {};
    tagData.type = property;
    switch (property) {
      case 'transport': 
        tagData.tag = this.currentPlace.transports.find(s => s.name === tagName);
        break;
      case 'stay': 
        tagData.tag = this.currentPlace.stays.find(s => s.name === tagName);
        break;
      case 'activity': 
        tagData.tag = this.currentPlace.activities.find(s => s.name === tagName);
        break;
    }
    tagData.people = this.trip.people.map(s => { 
      return { text: s.name, value: s.name } as DropdownOption;
    })
    this.offCanvasData = tagData;
    this.offcanvasdetailsComponent.showOffCanvas();
  }

  saveTagData(changes: offcanvas) {
    switch (changes.type) {
      case 'transport': 
        const indexTransport = this.currentPlace.transports.findIndex(s => s.name === changes.tag.name);
        const changedTransports = {...this.currentPlace.transports[indexTransport], comments: changes.tag.comments, expense: changes.tag.expense }
        const updatedTransports = [...this.currentPlace.transports.slice(0, indexTransport), changedTransports, ...this.currentPlace.transports.slice(indexTransport + 1)]
        this.currentPlace = {...this.currentPlace, transports: updatedTransports }
        this.reactiveForm.get('transports').markAsDirty();
        break;
      case 'stay': 
        const indexStay = this.currentPlace.stays.findIndex(s => s.name === changes.tag.name);
        const changedStays = {...this.currentPlace.stays[indexStay], comments: changes.tag.comments, expense: changes.tag.expense }
        const updatedStays = [...this.currentPlace.stays.slice(0, indexStay), changedStays, ...this.currentPlace.stays.slice(indexStay + 1)]
        this.currentPlace = {...this.currentPlace, stays: updatedStays }
        this.reactiveForm.get('stays').markAsDirty();
        break;
      case 'activity': 
        const indexActivity = this.currentPlace.activities.findIndex(s => s.name === changes.tag.name);
        const changedActivities = {...this.currentPlace.activities[indexActivity], comments: changes.tag.comments, expense: changes.tag.expense }
        const updatedActivities = [...this.currentPlace.activities.slice(0, indexActivity), changedActivities, ...this.currentPlace.activities.slice(indexActivity + 1)]
        this.currentPlace = {...this.currentPlace, activities: updatedActivities }
        this.reactiveForm.get('activities').markAsDirty();
        break;
    }
    this.setFormState();
  }

  onNext() {
    this.onNavigate.emit(true);
  }

  onPrevious() {
    this.onNavigate.emit(false);
  }

  saveTrip() {
    if (this.reactiveForm.valid) {
      if (this.reactiveForm.dirty) {
        const { fromDate, toDate } = this.reactiveForm.value;

        // const newTransports = this.currentPlace.transports ? [...this.currentPlace.transports?.filter((item) => transports.some((newItem) => newItem === item.name)), 
        //   ...transports.filter((newItem) => !this.currentPlace.transports?.some((item) => item.name === newItem))] :
        //   transports;

        let place: Place = {
          name: this.currentPlace.name,
          start_at: this.parserFormatter.format(fromDate),
          end_at: this.parserFormatter.format(toDate),
          transports: this.currentPlace.transports ?? null,
          stays: this.currentPlace.stays ?? null,
          activities: this.currentPlace.activities ?? null
        }

        this.store.dispatch(tripActions.updatePlace({place : { ...place}}));
        this.reactiveForm.markAsPristine();
        this.setFormState();
      }
    }
  }

  cancelChanges() {
    this.setupReactiveForm(true);
    this.setFormState()
  }

  // Datepicker
  hoveredDate: NgbDate | null = null;
  current = new Date();
  minDate = {
    year: this.current.getFullYear(),
    month: this.current.getMonth() + 1,
    day: this.current.getDate()
  };
  maxDate = this.minDate;

  isDisabled = (date: NgbDateStruct) => {
    const jsDate = new Date(date.year, date.month - 1, date.day);
    return jsDate < new Date(this.minDate.year, this.minDate.month - 1, this.minDate.day) ||
      jsDate > new Date(this.maxDate.year, this.maxDate.month - 1, this.maxDate.day);
  };

  // Date picker
  onDateSelection(date: NgbDate) {
		if (!this.reactiveForm.value.fromDate && !this.reactiveForm.value.toDate) {
			this.reactiveForm.patchValue({fromDate: date});
		} else if (this.reactiveForm.value.fromDate && !this.reactiveForm.value.toDate && date.after(this.reactiveForm.value.fromDate)) {
			this.reactiveForm.patchValue({toDate: date});
		} else {
			this.reactiveForm.patchValue({toDate: null});
			this.reactiveForm.patchValue({fromDate: date});
		}
    this.reactiveForm.get('fromDate').markAsDirty();
    this.reactiveForm.get('toDate').markAsDirty();
	}

  isHovered(date: NgbDate) {
		return (
			this.reactiveForm.value.fromDate && !this.reactiveForm.value.toDate && this.hoveredDate && date.after(this.reactiveForm.value.fromDate) && date.before(this.hoveredDate)
		);
	}

  isInside(date: NgbDate) {
		return this.reactiveForm.value.toDate && date.after(this.reactiveForm.value.fromDate) && date.before(this.reactiveForm.value.toDate);
	}

	isRange(date: NgbDate) {
		return (
			date.equals(this.reactiveForm.value.fromDate) ||
			(this.reactiveForm.value.toDate && date.equals(this.reactiveForm.value.toDate)) ||
			this.isInside(date) ||
			this.isHovered(date)
		);
	}
}
