import { createFeatureSelector, createSelector, props } from "@ngrx/store";
import { TripStoreState } from "../models/tripStoreState";

export const tripFeatureState = createFeatureSelector<TripStoreState>('trips');

export const selectIsLoading = createSelector(tripFeatureState, (state: TripStoreState) => state.isLoading);
export const selectTrips = createSelector(tripFeatureState, (state: TripStoreState) => state.trips);
export const selectError = createSelector(tripFeatureState, (state: TripStoreState) => state.error);
export const selectTripId = createSelector(tripFeatureState, (state: TripStoreState) => state.selectedTripId);
export const selectTripsChanged = createSelector(tripFeatureState, (state: TripStoreState) => state.tripsChanged);
export const selectTrip = (id: string) => createSelector(
    tripFeatureState, 
    (state: TripStoreState) => state.trips.find(trip => trip.id === id));
export const selectCurrentSelectedTrip = createSelector(
    tripFeatureState, 
    (state: TripStoreState) => state.trips.find(trip => trip.id === state.selectedTripId));