import { ChangeDetectionStrategy, Component, EventEmitter, Input, OnDestroy, OnInit, Output, ViewChild } from '@angular/core';
import { FormBuilder, FormControl, FormGroup, ReactiveFormsModule, Validators } from '@angular/forms';
import { NgbCalendar, NgbDate, NgbDatepicker, NgbDatepickerModule, NgbDateStruct, NgbDateParserFormatter } from '@ng-bootstrap/ng-bootstrap';
import { Trip } from 'src/app/models/trip.model';
import { CommonModule } from '@angular/common';
import { InputformtagComponent } from '../common/inputformtag/inputformtag.component';
import { componentBase } from '../componentBase';
import { Store } from '@ngrx/store';
import { TripStoreState } from 'src/app/models/tripStoreState';
import { AuthService } from 'src/app/services/auth.service';
import * as tripActions from 'src/app/store/trip.action';
import { ToastService } from 'src/app/services/toast.service';
import { Subscription } from 'rxjs';

@Component({
  selector: 'app-new-trip',
  standalone: true,
  imports: [CommonModule, ReactiveFormsModule, NgbDatepickerModule, InputformtagComponent],
  templateUrl: './new-trip.component.html',
  styleUrls: ['./new-trip.component.scss'],
  changeDetection: ChangeDetectionStrategy.OnPush
})
export class NewTripComponent extends componentBase implements OnInit, OnDestroy {
  @ViewChild('dpTravel') datepicker: NgbDatepicker;
  @Input() trip!: Trip;
  reactiveForm: FormGroup;
  @Output() onNavigate = new EventEmitter<boolean>();
  formValid: boolean = false;
  formDirty: boolean = false;
  formSubscription: Subscription

  constructor(store: Store<TripStoreState>, 
    authService: AuthService, private fb: FormBuilder, private calendar: NgbCalendar, private parserFormatter: NgbDateParserFormatter, private toastService: ToastService) {
      super(store, authService);
  }

  ngOnInit(): void {
    if (this.trip) {
      let places = this.trip.places.map(obj => obj.name);
      let people = this.trip.people.map(obj => obj.name);
      let startAt = new Date(this.trip.start_at);
      let endAt = new Date(this.trip.end_at);

      this.reactiveForm = this.fb.group({
        places: new FormControl<string[]>(places, [Validators.required]),
        people: new FormControl<string[]>(people, [Validators.required]),
        fromDate: new FormControl<NgbDate | null>(new NgbDate(startAt.getFullYear(), startAt.getMonth() + 1, startAt.getDate()), Validators.required),
        toDate: new FormControl<NgbDate | null>(new NgbDate(endAt.getFullYear(), endAt.getMonth() + 1, endAt.getDate()), Validators.required)
      }, );
    } else {
      this.reactiveForm = this.fb.group({
        places: new FormControl<string[]>([], [Validators.required]),
        people: new FormControl<string[]>([], [Validators.required]),
        fromDate: new FormControl<NgbDate | null>(this.calendar.getToday(), Validators.required),
        toDate: new FormControl<NgbDate | null>(this.calendar.getNext(this.calendar.getToday(), 'd', 5), Validators.required)
      });
    }

    this.formSubscription = this.reactiveForm.valueChanges.subscribe(data => {
      this.setFormState();
    })
  }

  setFormState() {
    this.formValid = this.reactiveForm.valid;
    this.formDirty = this.reactiveForm.dirty;
  }

  ngAfterViewInit() {
    if (this.trip) {
      this.datepicker.navigateTo({year: this.reactiveForm.value.fromDate.year, month: this.reactiveForm.value.fromDate.month});
    }
  }

  // Datepicker
  hoveredDate: NgbDate | null = null;
  current = new Date();
  minDate = {
    year: this.current.getFullYear(),
    month: this.current.getMonth() + 1,
    day: this.current.getDate()
  };
  
  isDisabled = (date: NgbDateStruct): boolean => {
    //below code for min and max date range working good.
    const jsDate = new Date(date.year, date.month - 1, date.day);
    let setDisable = jsDate < new Date(this.minDate.year, this.minDate.month - 1, this.minDate.day);
    return setDisable;
  };

  // Date picker
  onDateSelection(date: NgbDate) {
		if (!this.reactiveForm.value.fromDate && !this.reactiveForm.value.toDate) {
			this.reactiveForm.patchValue({fromDate: date});
		} else if (this.reactiveForm.value.fromDate && !this.reactiveForm.value.toDate && date.after(this.reactiveForm.value.fromDate)) {
			this.reactiveForm.patchValue({toDate: date});
		} else {
			this.reactiveForm.patchValue({toDate: null});
			this.reactiveForm.patchValue({fromDate: date});
		}
    this.reactiveForm.get('fromDate').markAsDirty();
    this.reactiveForm.get('toDate').markAsDirty();
	}

	isHovered(date: NgbDate) {
		return (
			this.reactiveForm.value.fromDate && !this.reactiveForm.value.toDate && this.hoveredDate && date.after(this.reactiveForm.value.fromDate) && date.before(this.hoveredDate)
		);
	}

	isInside(date: NgbDate) {
		return this.reactiveForm.value.toDate && date.after(this.reactiveForm.value.fromDate) && date.before(this.reactiveForm.value.toDate);
	}

	isRange(date: NgbDate) {
		return (
			date.equals(this.reactiveForm.value.fromDate) ||
			(this.reactiveForm.value.toDate && date.equals(this.reactiveForm.value.toDate)) ||
			this.isInside(date) ||
			this.isHovered(date)
		);
	}

  saveTrip() {
    if (this.reactiveForm.valid) {
      if (this.reactiveForm.dirty) {
        const { places, fromDate, toDate, people } = this.reactiveForm.value;
        const newPlaces = this.trip.places.filter(s => places.includes(s.name));
        const newPlacesNames = newPlaces.map(s => s.name);
        places.forEach((element : string) => {
          if (!newPlacesNames.includes(element)) {
            newPlaces.push({ name: element });
          }
        });

        const newPeople = this.trip.people.filter(s => people.includes(s.name));
        const newPeopleNames = newPeople.map(s => s.name);
        people.forEach((element : string) => {
          if (!newPeopleNames.includes(element)) {
            newPeople.push({ name: element });
          }
        });

        let trip: Trip = {
          places: newPlaces,
          start_at: this.parserFormatter.format(fromDate),
          end_at: this.parserFormatter.format(toDate),
          people: newPeople,
          userId: this.trip.userId,
          id: this.trip.id
        }
        this.store.dispatch(tripActions.updateTrip({trip : { ...trip}}));
        this.reactiveForm.markAsPristine();
        this.setFormState();
      }
    }
  }

  createTrip() {
    if (this.reactiveForm.valid && this.reactiveForm.dirty) {
      this.formDirty = false;
      const { places, fromDate, toDate, people } = this.reactiveForm.value;
      let trip: Trip = {
        places: places.map(str => ({ name: str })),
        start_at: this.parserFormatter.format(fromDate),
        end_at: this.parserFormatter.format(toDate),
        people: people.map(str => ({ name: str })),
        userId: this.authService.user.uid,
        id: null
      }

      this.store.dispatch(tripActions.newTrip({trip : { ...trip}}));
      if (!!this.error$) {
        this.toastService.showWarningToast("New trip created.", "Please continue capture other information in the Inprogress tab.");
        this.reactiveForm.markAsPristine();
        this.setFormState();
      } else {
        this.toastService.showWarningToast("Failed to Save", "Something went Wrong. Please report using feedback feature.");
        console.log(this.error$);
      }
    }
  }

  onNext() {
    this.onNavigate.emit(true);
  }

  cancelChanges() {
    const places = this.trip.places.map(obj => obj.name);
    const people = this.trip.people.map(obj => obj.name);
    const startAt = new Date(this.trip.start_at);
    const endAt = new Date(this.trip.end_at);
    this.reactiveForm.reset({
      places,
      people,
      fromDate: new NgbDate(startAt.getFullYear(), startAt.getMonth() + 1, startAt.getDate()),
      toDate: new NgbDate(endAt.getFullYear(), endAt.getMonth() + 1, endAt.getDate())
    });
  }

  ngOnDestroy(): void {
    if (this.formSubscription) {
      this.formSubscription.unsubscribe();
    }
  }
}
