import { Component, Input, forwardRef } from '@angular/core';
import { CommonModule } from '@angular/common';
import { startCase } from 'lodash';
import { AbstractControl, ControlValueAccessor, FormsModule, NG_VALIDATORS, NG_VALUE_ACCESSOR, ValidationErrors, Validator } from '@angular/forms';

@Component({
  selector: 'app-inputformtag',
  standalone: true,
  imports: [CommonModule, FormsModule],
  templateUrl: './inputformtag.component.html',
  styleUrls: ['./inputformtag.component.scss'],
  providers: [
    {
      provide: NG_VALUE_ACCESSOR,
      multi:true,
      useExisting: forwardRef(() => InputformtagComponent) 
    },
    {
      provide: NG_VALIDATORS,
      multi: true,
      useExisting: forwardRef(() => InputformtagComponent) 
    }
  ]
})
export class InputformtagComponent implements ControlValueAccessor, Validator {
  @Input() placeholderText!: string;

  userInput: string = '';
  tags: string[] = [];

  constructor() { }

  onChange = (tags) => {};

  onTouched = () => {};

  touched = false;

  disabled = false;

  writeValue(inputTags: string[]): void {
    this.tags = inputTags;
  }

  registerOnChange(onChangeFn: any): void {
    this.onChange = onChangeFn;
  }

  registerOnTouched(onTouchedFn: any): void {
    this.onTouched = onTouchedFn;
  }

  markAsTouched() {
    if (!this.touched) {
      this.onTouched();
      this.touched = true;
    }
  }

  setDisabledState?(isDisabled: boolean): void {
    this.disabled = isDisabled;
  }

  validate(control: AbstractControl<any, any>): ValidationErrors | null {
    return (this.tags?.length === 0) ? { tagsAreEmpty: true } : null;
  }

  // registerOnValidatorChange?(fn: () => void): void {
  //   throw new Error('Method not implemented.');
  // }

  ngOnInit(): void {
    
  }

  addtoTags() {
    this.markAsTouched();
    if (!this.disabled && this.userInput.length > 0) {
      let formatedInput = this.userInput.replace(/\w+/g, startCase);
      if (this.tags?.indexOf(formatedInput) < 0) {
        this.tags = [...this.tags, formatedInput]
        this.onChange(this.tags);
      }
    } 
    this.userInput = "";
  }

  removeTag(value: string) {
    this.markAsTouched();
    this.tags = this.tags.filter(s => s != value);
    this.onChange(this.tags);
  }
}