import { createReducer, on } from "@ngrx/store";
import { TripStoreState } from "../models/tripStoreState";
import * as tripActions from "./trip.action";

export const initialState: TripStoreState = {
    isLoading: false,
    trips: [],
    error: null,
    selectedTripId: null,
    tripsChanged: true
};

export const tripReducer = createReducer(
    initialState,
    on(tripActions.getTrips, (state) => {
        return {...state, isLoading: true };
    }),
    on(tripActions.getPastTrips, (state) => {
        return {...state, trips: [], tripsChanged: true, isLoading: true };
    }),
    on(tripActions.getTripsSuccessResponse, (state, action) => {
        return {...state, isLoading: false, trips: action.trips, tripsChanged: false };
    }),
    on(tripActions.newTrip, (state) => {
        return {...state, isLoading: true };
    }),
    on(tripActions.newTripSuccessResponse, (state, { trip, id}) => {
        let trips = [...state.trips];
        let newTrip = {...trip, id};
        trips.unshift(newTrip);
        return {...state, isLoading: false, trips, selectedTripId: id };
    }),
    on(tripActions.getFailureResponse, (state, action) => {
        return {...state, isLoading: false, error: action.error };
    }),
    on(tripActions.updateTrip, (state, changes) => ({
        ...state,
        trips: state.trips.map(trip => {
            if (trip.id === state.selectedTripId) {
                return {
                    ...trip, ...changes.trip
                }
            }
            return trip;
        }),
        tripsChanged: true
    })),
    on(tripActions.updatePlace, (state, changes) => ({
        ...state,
        trips: state.trips.map(trip => {
            if (trip.id === state.selectedTripId) {
                const index = trip.places.findIndex(s => s.name === changes.place.name);
                return {
                    ...trip, places: [...trip.places.slice(0, index), changes.place, ...trip.places.slice(index + 1)]
                }
            }
            return trip;
        }),
        tripsChanged: true
    })),
    on(tripActions.updateBag, (state, changes) => ({
        ...state,
        trips: state.trips.map(trip => {
            if (trip.id === state.selectedTripId) {
                return {
                    ...trip, bag: changes.bag
                }
            }
            return trip;
        }),
    })),
    on(tripActions.updatePrepareList, (state, changes) => ({
        ...state,
        trips: state.trips.map(trip => {
            if (trip.id === state.selectedTripId) {
                return {
                    ...trip, prepareCheck: changes.prepareCheck
                }
            }
            return trip;
        }),
    })),
    on(tripActions.updateTripSuccessResponse, (state) => {
        return state;
    }),
    on(tripActions.selectTrip, (state, { tripId }) => {
        return {...state, selectedTripId: tripId}
    }),
    on(tripActions.clearTripSelection, (state) => {
        return (!!state.selectedTripId) ? {...state, selectedTripId: null} : state;
    }),
);  