import { Injectable, NgZone } from '@angular/core';
import { AuthErrorCodes } from 'firebase/auth';
import { AngularFireAuth } from '@angular/fire/compat/auth';
import { Router } from '@angular/router';
import { LocalstorageService } from './localstorage.service';
import { ToastService } from './toast.service';

@Injectable({
  providedIn: 'root'
})
export class AuthService {
  user: any;
  
  constructor(private afAuth: AngularFireAuth, private localStoreageService: LocalstorageService, private ngZone: NgZone, private router: Router, private toastService: ToastService) { 
    this.user = this.localStoreageService.getUserLoggedIn()
    this.afAuth.authState.subscribe(user => {
      if (user && user.emailVerified) {
        this.user = user;
        this.localStoreageService.setUserLoggedIn(this.user);
        this.ngZone.run(() => {
            this.router.navigate(['trip']);
        })
      } else {
        this.user = null;
        this.localStoreageService.clearUser();
        this.ngZone.run(() => {
          this.router.navigate(['login']);
        })
      }
    })
  }

  async SignIn(email: string, password: string) {
    return this.afAuth.signInWithEmailAndPassword(email, password)
      .then(result => {
        if (result.user && !result.user.emailVerified) {
          this.SignOut();
          this.toastService.showWarningToast("Login failed", "Email not verified. Please find the email in your inbox/junk/spam and do verify by click on the link.");
        }
      },(err) => {
        switch(err.code) {
          case AuthErrorCodes.INVALID_PASSWORD: {
            this.toastService.showWarningToast("Login failed", "Invalid Password.");
            break;
          }
          case AuthErrorCodes.INVALID_EMAIL: {
            this.toastService.showWarningToast("Login failed", "Invalid Email.");
            break;
          }
          case AuthErrorCodes.INVALID_IDP_RESPONSE: {
            this.toastService.showWarningToast("Login failed", "Invalid Email or Password.");
            break;
          }
          case AuthErrorCodes.USER_MISMATCH: {
            this.toastService.showWarningToast("Login failed", "User not found.");
            break;
          }
          case AuthErrorCodes.USER_DELETED: {
            this.toastService.showWarningToast("Login failed", "User not found.");
            break;
          }
          case AuthErrorCodes.USER_DISABLED: {
            this.toastService.showWarningToast("Login failed", "The user account has been disabled by an administrator.");
            break;
          }
          case AuthErrorCodes.UNVERIFIED_EMAIL: {
            this.toastService.showWarningToast("Login failed", "Email not verified. Please find the email in your inbox/junk/spam and do verify by click on the link.");
            break;
          }
        }
      })
      .catch (err => {
        console.log(err.message);
        this.toastService.showWarningToast("Login failed", "Something went Wrong. Please report using feedback feature.");
      })
  }

  async SignUp(email: string, password: string, displayName: string) {
    return this.afAuth.createUserWithEmailAndPassword(email, password)
      .then(result => {
        if (result.user){
          result.user.updateProfile({displayName: displayName}).then(() => {
              return result.user?.sendEmailVerification().then(() => {
                this.toastService.showSuccessToast("Email verification sent!", `Check out your Mailbox to verify the mail, If you did not find the email, check the junk folder, Spam.`)
                this.SignOut();
            })
          })
        }
      }, (err) => {
        switch(err.code) {
          case AuthErrorCodes.EMAIL_EXISTS: {
            this.toastService.showWarningToast("Registration failed", "Email already exists.");
            break;
          }
          case AuthErrorCodes.WEAK_PASSWORD: {
            this.toastService.showWarningToast("Registration failed", "Weak Password (Should be at least 6 characters long).")
            break;
          }
        }
      })
      .catch (err => {
        console.log(err.message);
        this.toastService.showWarningToast("Registration failed", "Something went Wrong. Please report using feedback feature.");
      })
  }

  async ForgotPassword(email: string) {
    return this.afAuth.sendPasswordResetEmail(email)
    .then(() => {
      this.toastService.showWarningToast("Password reset", "Password reset email sent. Please check your mailbox.");
      this.ngZone.run(() => {
        this.router.navigate(['login']);
      })
    }, (err) => {
      switch(err.code) {
        case AuthErrorCodes.USER_DELETED: {
          this.toastService.showWarningToast("Password reset", "Email not found.");
          break;
        }
      }
    })
  }

  async SignOut(){
    return this.afAuth.signOut();
  }
}
